export default class LayoutSwitcher {

	constructor( element ) {
		this.element = element;
	}

	place() {
		[
			'.wrap > a.page-title-action',
			'.wrap h1',
			'.wrap h2',
			'.wrap div'
		].some( selector =>{
			return this.tryPlacement( selector )
		});
	}

	tryPlacement( selector ) {
		let predecessor = document.querySelector( selector );

		if ( !predecessor ) {
			return false;
		}

		insertAfter( this.element, predecessor );
		this.element.classList.add( '-moved' );
		return true;
	}

}

function insertAfter( newNode, referenceNode ) {
	referenceNode.parentNode.insertBefore( newNode, referenceNode.nextSibling );
}