import Refresher from "./table/refresher";
import LayoutSwitcher from "./table/layout-switcher";

document.addEventListener( 'AC_Table_Ready', e => {
	let table = e.detail.table;

	table.Refresher = new Refresher( table );

	let switcher = document.querySelector( '.layout-switcher' );
	if ( switcher ) {
		let layout_switcher = new LayoutSwitcher( switcher );
		layout_switcher.place();
	}

} );
