export default class Refresher {

	constructor( table ) {
		this.table = table;
	}

	getRows( ids ) {
		return jQuery.ajax( {
			url : window.location.href,
			method : 'post',
			data : {
				ac_action : 'get_table_rows',
				ac_ids : ids,
				_ajax_nonce : AC.ajax_nonce
			}
		} );
	}

	updateRow( id ) {
		let ajax = this.getRows( [ id ] );

		ajax.done( response => {
			if ( response.success ) {
				this.populateRow( id, response.data.table_rows[ id ] );
			}
		} );
	}

	populateRow( id, rowdata ) {
		let element = document.createElement( 'table' );
		element.insertAdjacentHTML( 'beforeend', rowdata );

		this.table.Cells.getByID( 126 ).forEach( cell => {
			let td = element.querySelector( `td.column-${cell.getName()}` );
			cell.setValue( td.innerHTML );
		} );
	}

}